import { useCallback } from 'react'
import { useGame } from '../context/GameContext'
import { debug, info, warn, error } from '../utils/logger'

let nextDriverId = 1
let nextRiderId = 1
let nextRideId = 1

export const useTrafficSimulator = () => {
  const {
    worldSize,
    camera,
    drivers,
    riders,
    addDriver,
    addRider,
    addRideRequest,
    updateDriver,
    updateRider,
    removeDriver,
    removeRider,
    removeRideRequest,
    cleanMap: cleanMapContext,
    setBuildings,
    setRoads,
    setStreetNames,
    setLandmarks
  } = useGame()
  
  // Helper function to get current viewport bounds
  const getViewportBounds = useCallback(() => {
    // Calculate viewport dimensions (same logic as SVGGameCanvas)
    const viewportWidth = window.innerWidth - 56 - 300 // left toolbar + right panel
    const viewportHeight = window.innerHeight - 48 - 32 // top banner + bottom bar
    
    // Ensure viewport dimensions are positive
    const safeViewportWidth = Math.max(100, viewportWidth)
    const safeViewportHeight = Math.max(100, viewportHeight)
    
    // Calculate viewport bounds in world coordinates
    const halfViewWidth = (safeViewportWidth / camera.zoom) / 2
    const halfViewHeight = (safeViewportHeight / camera.zoom) / 2
    
    // Clamp camera position to world bounds
    const clampedCameraX = Math.max(halfViewWidth, Math.min(worldSize.width - halfViewWidth, camera.x))
    const clampedCameraY = Math.max(halfViewHeight, Math.min(worldSize.height - halfViewHeight, camera.y))
    
    return {
      minX: Math.max(0, clampedCameraX - halfViewWidth),
      maxX: Math.min(worldSize.width, clampedCameraX + halfViewWidth),
      minY: Math.max(0, clampedCameraY - halfViewHeight),
      maxY: Math.min(worldSize.height, clampedCameraY + halfViewHeight)
    }
  }, [camera, worldSize])
  
  // Initialize the game world
  const initializeGame = useCallback(() => {
    info(' Initializing Traffic Simulator...')
    
    // Create roads
    const roads = []
    // Horizontal roads
    for (let i = 0; i < 14; i++) {
      roads.push({
        x: 0,
        y: 84 + i * 100,
        width: worldSize.width,
        height: 32
      })
    }
    // Vertical roads
    for (let i = 0; i < 16; i++) {
      roads.push({
        x: 100 + i * 140,
        y: 0,
        width: 32,
        height: worldSize.height
      })
    }
    setRoads(roads)
    
    // Create buildings
    const buildings = []
    for (let row = 0; row < 13; row++) {
      for (let col = 0; col < 15; col++) {
        const blockX = 132 + col * 140
        const blockY = 116 + row * 100
        
        // Add 1-3 buildings per block
        const buildingCount = Math.floor(Math.random() * 3) + 1
        for (let b = 0; b < buildingCount; b++) {
          const width = 40 + Math.random() * 40
          const height = 30 + Math.random() * 30
          const offsetX = Math.random() * (70 - width)
          const offsetY = Math.random() * (36 - height)
          
          buildings.push({
            x: blockX + offsetX,
            y: blockY + offsetY,
            width,
            height
          })
        }
      }
    }
    setBuildings(buildings)
    
    // Create street names
    const streetNames = []
    const horizontalStreets = ['Main St', 'Broadway', 'Oak Ave', 'Pine St', 'Elm St', 'Maple Ave', 'Cedar St', 'First St', 'Second St', 'Third St', 'Fourth St', 'Fifth St', 'Park Ave', 'Central St']
    const verticalStreets = ['First Ave', 'Second Ave', 'Third Ave', 'Fourth Ave', 'Fifth Ave', 'Central Ave', 'North Ave', 'South Ave', 'East Ave', 'West Ave', 'Grand Ave', 'Union Ave', 'Liberty Ave', 'Washington Ave', 'Lincoln Ave', 'Jefferson Ave']
    
    horizontalStreets.forEach((name, i) => {
      const y = 84 + i * 100 + 16
      streetNames.push({ name, x: worldSize.width / 2, y })
    })
    
    verticalStreets.forEach((name, i) => {
      const x = 100 + i * 140 + 16
      streetNames.push({ name, x, y: worldSize.height / 2 })
    })
    
    setStreetNames(streetNames)
    
    // Create landmarks
    const landmarks = [
      { name: 'Downtown', icon: '', x: 600, y: 400 },
      { name: 'Airport', icon: '', x: 1800, y: 800 },
      { name: 'Mall', icon: '', x: 1200, y: 1200 },
      { name: 'Station', icon: '', x: 400, y: 1000 },
      { name: 'Hospital', icon: '', x: 2000, y: 400 },
      { name: 'University', icon: '', x: 800, y: 1400 }
    ]
    setLandmarks(landmarks)
    
    info(' Traffic Simulator initialized!')
  }, [worldSize, setBuildings, setRoads, setStreetNames, setLandmarks])
  
  // Spawn a rider
  const spawnRider = useCallback(() => {
    const bounds = getViewportBounds()
    
    // Add some padding from viewport edges (50px)
    const padding = 50
    const x = Math.random() * (bounds.maxX - bounds.minX - padding * 2) + bounds.minX + padding
    const y = Math.random() * (bounds.maxY - bounds.minY - padding * 2) + bounds.minY + padding
    
    const rider = {
      id: nextRiderId++,
      x,
      y,
      status: 'idle'
    }
    
    addRider(rider)
    info(` Spawned rider #${rider.id} at (${Math.round(x)}, ${Math.round(y)}) in viewport`)
  }, [getViewportBounds, addRider])
  
  // Spawn a driver
  const spawnDriver = useCallback((type = null) => {
    const bounds = getViewportBounds()
    
    // Add some padding from viewport edges (50px)
    const padding = 50
    const x = Math.random() * (bounds.maxX - bounds.minX - padding * 2) + bounds.minX + padding
    const y = Math.random() * (bounds.maxY - bounds.minY - padding * 2) + bounds.minY + padding
    
    // Randomly choose type if not specified (50/50 chance)
    const driverType = type || (Math.random() < 0.5 ? 'ground' : 'air')
    
    const driver = {
      id: nextDriverId++,
      x,
      y,
      type: driverType, // 'ground' or 'air'
      status: 'idle',
      speed: driverType === 'air' ? 200 : 120 // Air is faster
    }
    
    addDriver(driver)
    const emoji = driverType === 'air' ? '' : ''
    const unitName = driverType === 'air' ? 'pilot' : 'driver'
    info(`${emoji} Spawned ${driverType} ${unitName} #${driver.id} at (${Math.round(x)}, ${Math.round(y)}) in viewport`)
  }, [getViewportBounds, addDriver])
  
  // Create a ride request
  const createRideRequest = useCallback((preferredType = null) => {
    // Find an idle rider
    const idleRiders = riders.filter(r => r.status === 'idle')
    if (idleRiders.length === 0) {
      warn(' No idle riders available!')
      return
    }
    
    const rider = idleRiders[Math.floor(Math.random() * idleRiders.length)]
    
    // Randomly choose ride type if not specified (60% ground, 40% air)
    const rideType = preferredType || (Math.random() < 0.6 ? 'ground' : 'air')
    
    const pickupX = rider.x
    const pickupY = rider.y
    
    // Use viewport bounds for dropoff location
    const bounds = getViewportBounds()
    const padding = 50
    const dropoffX = Math.random() * (bounds.maxX - bounds.minX - padding * 2) + bounds.minX + padding
    const dropoffY = Math.random() * (bounds.maxY - bounds.minY - padding * 2) + bounds.minY + padding
    
    const distance = Math.sqrt(
      Math.pow(dropoffX - pickupX, 2) + Math.pow(dropoffY - pickupY, 2)
    )
    
    // Air is more expensive per km (0.15 vs 0.1)
    const pricePerKm = rideType === 'air' ? 0.15 : 0.1
    const fare = Math.round(distance * pricePerKm) || 10
    
    const ride = {
      id: nextRideId++,
      riderId: rider.id,
      type: rideType, // 'ground' or 'air'
      pickupX,
      pickupY,
      dropoffX,
      dropoffY,
      fare,
      distance,
      pricePerKm,
      assignedDriver: null,
      status: 'waiting_for_pickup',
      createdAt: Date.now()
    }
    
    // Update rider status
    updateRider(rider.id, { status: 'waiting' })
    
    // Find closest idle driver of the SAME type and assign
    const idleDrivers = drivers.filter(d => d.status === 'idle' && d.type === rideType)
    if (idleDrivers.length > 0) {
      let closestDriver = null
      let closestDistance = Infinity
      
      idleDrivers.forEach(d => {
        const dist = Math.sqrt(
          Math.pow(d.x - pickupX, 2) + Math.pow(d.y - pickupY, 2)
        )
        if (dist < closestDistance) {
          closestDistance = dist
          closestDriver = d
        }
      })
      
      if (closestDriver) {
        ride.assignedDriver = closestDriver
        ride.status = 'going_to_rider'
        updateDriver(closestDriver.id, {
          status: 'going_to_rider',
          targetX: pickupX,
          targetY: pickupY
        })
        const emoji = rideType === 'air' ? '' : ''
        const unitName = rideType === 'air' ? 'Pilot' : 'Driver'
        info(`${emoji} ${unitName} #${closestDriver.id} assigned to ${rideType} ride #${ride.id}`)
      }
    }
    
    addRideRequest(ride)
    const emoji = rideType === 'air' ? '' : ''
    info(` Created ${rideType} ride request #${ride.id}: $${fare} ${emoji}`)
  }, [getViewportBounds, addRideRequest, riders, drivers, updateRider, updateDriver])
  
  // Clean the map
  const cleanMap = useCallback(() => {
    cleanMapContext()
    nextDriverId = 1
    nextRiderId = 1
    nextRideId = 1
    info(' Map cleaned!')
  }, [cleanMapContext])
  
  return {
    initializeGame,
    spawnRider,
    spawnDriver,
    createRideRequest,
    cleanMap
  }
}

